  height: 1em;
  padding: 0 8px;
}

.bjs-breadcrumbs .bjs-crumb {
  max-width: 200px;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
  overflow: hidden;
}

.bjs-drilldown {
  width: 20px;
  height: 20px;
  cursor: pointer;
  fill: var(--drilldown-fill-color);
  background-color: var(--drilldown-background-color);
  border: none;
  border-radius: 2px;
  outline: none;
  margin-left: -20px;
  padding: 0;
}

.bjs-drilldown-empty {
  display: none;
}

.selected .bjs-drilldown-empty {
  display: inherit;
}

[data-popup="align-elements"] .djs-popup-results {
